@font-face {
  font-family: 'Roboto Condensed';
  font-weight: 400;
  font-style: normal;

  src:
    local('Roboto Condensed'),
    local('RobotoCondensed-Regular'),
    url('../fonts/Roboto_Condensed/Robotocondensed.woff2') format('woff2'),
    url('../fonts/Roboto_Condensed/Robotocondensed.woff') format('woff');
}

@font-face {
  font-family: 'Roboto Condensed';
  font-weight: 700;
  font-style: normal;

  src:
    local('Roboto Condensed Bold'),
    local('RobotoCondensed-Bold'),
    url('../fonts/Roboto_Condensed/Robotocondensedbold.woff2') format('woff2'),
    url('../fonts/Roboto_Condensed/Robotocondensedbold.woff') format('woff');
}

@font-face {
  font-family: 'PT Mono';
  font-weight: 400;
  font-style: normal;

  src:
    local('PT Mono'),
    local('PTMono-Regular'),
    url('../fonts/PT_Mono/ptmono.woff2') format('woff2'),
    url('../fonts/PT_Mono/ptmono.woff') format('woff');
}

@font-face {
  font-family: 'PT Mono';
  font-weight: 700;
  font-style: normal;

  src:
    local('PT Mono Bold'),
    local('PTMono-Bold'),
    url('../fonts/PT_Mono/ptmonobold.woff2') format('woff2'),
    url('../fonts/PT_Mono/ptmonobold.woff') format('woff');
}
